(function() {
	'use strict';

	angular
		.module('ingeSoftIi', ['ngAnimate', 'ngMessages', 'ngResource', 'ngRoute', 'ngMaterial', 'toastr', 'ui.bootstrap', 'uiGmapgoogle-maps', 'vAccordion', 'firebase']);

	angular.module("uib/template/rating/rating.html", []).run(["$templateCache", function($templateCache) {
		$templateCache.put("uib/template/rating/rating.html",
		"<span ng-mouseleave=\"reset()\" ng-keydown=\"onKeydown($event)\" tabindex=\"0\" role=\"slider\" aria-valuemin=\"0\" aria-valuemax=\"{{range.length}}\" aria-valuenow=\"{{value}}\" aria-valuetext=\"{{title}}\">\n" +
		"    <span ng-repeat-start=\"r in range track by $index\" class=\"sr-only\"></span>\n" +
		"    <i ng-repeat-end ng-mouseenter=\"enter($index + 1)\" ng-click=\"rate($index + 1)\" class=\"glyphicon\" ng-class=\"$index < value && (r.stateOn || 'glyphicon-star') || (r.stateOff || 'glyphicon-star-empty')\" ng-attr-title=\"{{r.title}}\"></i>\n" +
		"</span>\n" +
		"");
	}]);
})();



(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('ViewUserController', ViewUserController);

	/** @ngInject */
	function ViewUserController(UserService, ChatService, $location, toastr, $mdDialog) {
		var vm = this;

		vm.openEditDemandDialog = openEditDemandDialog;
		vm.openDeleteDemandDialog = openDeleteDemandDialog;
		vm.openEditOfferDialog = openEditOfferDialog;
		vm.openDeleteOfferDialog = openDeleteOfferDialog;
		vm.openEditProfileDialog = openEditProfileDialog;
		vm.startChar = startChar;
		
		
		function loadProfile(profileID){
			UserService.getUserByID(profileID)
				.then(function(profile){
					vm.profile = profile;
					ChatService.getUserConectionStatus(profile.username)
						.then(function(isOnline){
							vm.profile.isOnline = isOnline;
						});
				});
		}
		
		function openEditOfferDialog($event, offerID){
			$mdDialog.show({
				controller: 'UpdateOfferController',
				controllerAs: 'vm',
				templateUrl: 'app/offer/update/update_offer.html',
				targetEvent: $event,
				clickOutsideToClose:true,
				locals: {
					offerID: offerID
				}
			})
			.finally(detectProfile);
		}
		
		function openDeleteOfferDialog($event, offerID){
			$mdDialog.show({
				controller: 'DeleteOfferController',
				controllerAs: 'vm',
				templateUrl: 'app/offer/delete/delete_offer.html',
				targetEvent: $event,
				clickOutsideToClose:true,
				locals: {
					offerID: offerID
				}
			})
			.finally(detectProfile);
		}
		
		function openEditDemandDialog($event, demandID){
			$mdDialog.show({
				controller: 'UpdateDemandController',
				controllerAs: 'vm',
				templateUrl: 'app/demands/update/update_demand.html',
				targetEvent: $event,
				clickOutsideToClose:true,
				locals: {
					demandID: demandID
				}
			})
			.finally(detectProfile);
		}
		

		function openDeleteDemandDialog($event, demandID){
			$mdDialog.show({
				controller: 'DeleteDemandController',
				controllerAs: 'vm',
				templateUrl: 'app/demands/delete/delete_demand.html',
				targetEvent: $event,
				clickOutsideToClose:true,
				locals: {
					demandID: demandID
				}
			})
			.finally(detectProfile);
		}
		
		function openEditProfileDialog($event){
			$mdDialog.show({
				controller: 'EditProfileController',
				controllerAs: 'vm',
				templateUrl: 'app/user/edit/edit_profile.html',
				targetEvent: $event,
				clickOutsideToClose:true,
				locals: {
					userID: vm.profile.id
				}
			})
			.finally(detectProfile);
		}
		
		function startChar(){
			var fromUser = angular.fromJson(sessionStorage.user).username;
			var toUser = vm.profile.username;
			$location.url("/chat");
			$location.search("fromUser",fromUser);
			$location.search("toUser",toUser);
		}
		
		function detectProfile(){
			var profileID;
			if($location.search().id){
				profileID = $location.search().id;
				loadProfile(profileID);
				vm.showEdit = false;
			}
			else{
				vm.showEdit = true;
				try{
					profileID = angular.fromJson(sessionStorage.user).id;
					loadProfile(profileID);
				}
				catch(err){
					toastr.error('Hubo un error al cargar los datos del usuario!', 'Error!');
				}
			}
		}
		
		detectProfile();
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('RegisterUserController', RegisterUserController);
	
	/** @ngInject */
	function RegisterUserController(UserService, toastr, $location) {
		var vm = this;
		
		vm.profile = {gender: "M", admin: false};
		vm.genders = UserService.getAllGenders();
		vm.registerUser = registerUser;
		
		function registerUser(){
			UserService.createUser(vm.profile)
				.then(function(){
					toastr.success('El usuario fue registrado con exito', 'Usuario creado');
					$location.url("/login");
				})
				.catch(function(){
					toastr.error('Hubo un error al intentar registrar el usuario!', 'Error!');
				});
		}
	}
})();

(function () {
  'use strict';
  angular
    .module('ingeSoftIi')
    .controller('EditProfileController',EditProfileController);

  /** @ngInject */
  function EditProfileController(UserService,$mdDialog,toastr,userID) {
    var vm = this;

    vm.updateUser = updateUser;
    vm.closeDialog = closeDialog;
	vm.genders = UserService.getAllGenders();

    function updateUser(){
      UserService.updateUser(vm.profile)
        .then(function(){
          toastr.success('El usuario fue actualizado con exito', 'Usuario actualizado');
        })
        .catch(function(){
          toastr.error('Hubo un error al intentar actualizar el usuario!', 'Error!');
        });
      $mdDialog.hide();
    }
	
	function loadUser(){
		UserService.getUserByID(userID)
			.then(function(profile){
				vm.profile = profile;
			});
    }
	
    function closeDialog(){
      $mdDialog.cancel();
    }
	
	loadUser();
  }
})();
