(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.service('UserService', UserService);
	
	/** @ngInject */
	function UserService($resource, backend_url) {
		var service = {
			getAllUsers: getAllUsers,
			getUserByID: getUserByID,
			createUser: createUser,
			updateUser: updateUser,
			deleteUser: deleteUser,
			getAllGenders: getAllGenders
		};
		
		var UserResource =  $resource(backend_url+'/users/:id', {}, {
			query: { method: 'GET', isArray: true },
			create: { method: 'POST' },
			show: { method: 'GET' },
			update: { method: 'PUT', params: {id: '@id'} },
			delete: { method: 'DELETE', params: {id: '@id'} }
		});
		
		return service;
		
		function getAllUsers(){
			return UserResource.query().$promise;
		}
		
		function getUserByID(ID){
			return UserResource.show({id:ID}).$promise;
		}
		
		function createUser(User){
			return UserResource.create(User).$promise;
		}
		
		function updateUser(User){
			return UserResource.update(User).$promise;
		}
		
		function deleteUser(UserID){
			return UserResource.delete({id:UserID}).$promise;
		}
		
		function getAllGenders(){
			return [{id: 'M', name: 'Hombre'},{id: 'F', name: 'Mujer'}];
		}
		
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('MainController', MainController);

	/** @ngInject */
	function MainController(ChatService, $mdSidenav, app_menu, $location, $rootScope, $scope, toastr, $mdDialog) {
		var vm = this;
		
		var chatListener;
		vm.toggleMenu = toggleMenu;
		vm.changeLocation = changeLocation;

		function toggleMenu() {
			$mdSidenav('left').toggle();
		}

		function changeLocation(url){
			$location.url(url);
			toggleMenu();
		}
		
		function showLogout(){
			vm.menus = _.reject(app_menu, function(menu){
				return menu.url === "/login" || menu.url === "/register";
			});
		}
		
		function showLogin(){
			vm.menus = _.reject(app_menu, function(menu){
				return menu.url === "/logout" || menu.url === "/viewUser";
			});
		}
		
		function startChatListener(){
			ChatService.setUserConectionStatus(true);
			chatListener = ChatService.startChatListener(redirectChat);			
		}
		
		function stopChatListener(){
			ChatService.setUserConectionStatus(false);
			ChatService.stopChatListener(chatListener);
		}
		
		function redirectChat(chatRegister) {
			if($location.url().indexOf("/chat") !== 0){
				var fromUser = chatRegister.key();
				
				var confirmChat = $mdDialog.confirm()
					.title("Solicitud de chat entrante")
					.textContent("El usuario "+fromUser+" desea chatear contigo")
					.ok("Iniciar chat")
					.cancel("Ignorar");
					
				$mdDialog.show(confirmChat).then(function() {
					var toUser = angular.fromJson(sessionStorage.user).username;
					$location.url("/chat");
					$location.search("fromUser",fromUser);
					$location.search("toUser",toUser);
				}, function() {
					toastr.info("El chat del usuario "+fromUser+" ha sido ignorado");
				});
			}
		}
		
		function detectSession(){
			if(sessionStorage.user && $location.url() !== "/logout"){
				showLogout();
				startChatListener();
			}
			else{
				showLogin();
			}
		}
		
		detectSession();
		
		var onLogin = $rootScope.$on("login",showLogout);
		var onLogout = $rootScope.$on("logout",showLogin);
		var runChat = $rootScope.$on("runChat",startChatListener);
		var stopChat = $rootScope.$on("stopChat",stopChatListener);
		
		$scope.$on('$destroy', function(){
			onLogin();
			onLogout();
			runChat();
			stopChat();
		});
	}
})();
(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.service('OfferService', OfferService);

	/** @ngInject */
	function OfferService($resource,backend_url) {
		var service = {
			getAllOffers: getAllOffers,
			getOffersByID: getOffersByID,
			createOffer: createOffer,
			updateOffer: updateOffer,
			deleteOffer: deleteOffer
		};
		
		var OfferResource =  $resource(backend_url+'/offers/:id', {}, {
			query: { method: 'GET', isArray: true },
			create: { method: 'POST' },
			show: { method: 'GET' },
			update: { method: 'PUT', params: {id: '@id'} },
			delete: { method: 'DELETE', params: {id: '@id'} }
		});
		
		return service;
		
		function getAllOffers(){
			return OfferResource.query().$promise;
		}
		
		function getOffersByID(ID){
			return OfferResource.show({id:ID}).$promise;
		}
		
		function createOffer(offer){
			offer = angular.copy(offer);
			offer.deadline = offer.deadline.getTime();
			return OfferResource.create(offer).$promise;
		}
		
		function updateOffer(offer){
			offer.deadline = offer.deadline.getTime();
			return OfferResource.update(offer).$promise;
		}
		
		function deleteOffer(offerID){
			return OfferResource.delete({id:offerID}).$promise;
		}
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('OfferController', OfferController);

	/** @ngInject */
	function OfferController(OfferService, $mdDialog, $location, $scope) {
		var vm = this;
		
		vm.openCreateDialog = openCreateDialog;
		vm.openViewDialog = openViewDialog;
		vm.search = {};
		
		function loadOffers(){
			OfferService.getAllOffers()
				.then(function(offers){
					vm.offers = offers;
					loadFilters();
				});
		}
		
		function openViewDialog($event, offerID){
			$mdDialog.show({
				controller: 'ViewOfferController',
				controllerAs: 'vm',
				templateUrl: 'app/offer/view/viewOffer.html',
				targetEvent: $event,
				clickOutsideToClose:true,
				locals: {
					offerID: offerID
				}
			})
			.finally(function() {
				loadOffers();
			});
		}
		
		function openCreateDialog($event){			
			$mdDialog.show({
				controller: 'CreateOfferController',
				controllerAs: 'vm',
				templateUrl: 'app/offer/create/createOffer.html',
				targetEvent: $event,
				clickOutsideToClose:true
			})
			.finally(function() {
				loadOffers();
			});
		}
		
		function loadFilters(){
			if($location.search().search){
				vm.search.title = $location.search().search;
				$scope.accordion.toggle(0);
			}
		}
		
		loadOffers();
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.service('LoginService', LoginService);

	function LoginService($resource, backend_url) {
		var service = {
			makeLogin: makeLogin
		};
		
		var LoginResource =  $resource(backend_url+'/users/login', {}, {
			login: { method: 'POST' }
		});
		
		return service;
		
		function makeLogin(userData){
			return LoginResource.login(userData).$promise;
		}
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('LoginController', LoginController);

	function LoginController(LoginService, $location, toastr, $rootScope) {
		var vm = this;
		vm.userData = {};
		vm.login = login;
		vm.redirect = redirect;
		
		function login(){
			LoginService.makeLogin(vm.userData)
				.then(function(userInfo){
					sessionStorage.user = angular.toJson(userInfo);
					$rootScope.$emit("runChat");
					$rootScope.$emit("login");
					redirect('/offer');
				})
				.catch(function(error){
					if(error.status === 403){
						toastr.error('Nombre de usuario o contraseña incorrectas!', 'Error!');
					}
					else{
						toastr.error('Hubo un error al intentar acceder a su cuenta!', 'Error!');
					}
				});
		}
		
		function redirect(location){
			$location.url(location);
		}
		
		function detectSession(){
			if($location.url() === "/logout"){
				$rootScope.$emit("stopChat");
				sessionStorage.removeItem("user");
				$rootScope.$emit("logout");
			}
		}
		detectSession()
		
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.service('DemandsService', DemandsService);

	/** @ngInject */
	function DemandsService($resource,backend_url) {
		var service = {
			getAllDemands: getAllDemands,
			getDemandsByID: getDemandsByID,
			createDemand: createDemand,
			updateDemand: updateDemand,
			deleteDemand: deleteDemand
		};
		
		var DemandResource =  $resource(backend_url+'/demands/:id', {}, {
			query: { method: 'GET', isArray: true },
			create: { method: 'POST' },
			show: { method: 'GET' },
			update: { method: 'PUT', params: {id: '@id'} },
			delete: { method: 'DELETE', params: {id: '@id'} }
		});
		
		return service;
		
		function getAllDemands(){
			return DemandResource.query().$promise;
		}
		
		function getDemandsByID(ID){
			return DemandResource.show({id:ID}).$promise;
		}
		
		function createDemand(demand){
			demand = angular.copy(demand);
			demand.deadline = demand.deadline.getTime();
			return DemandResource.create(demand).$promise;
		}
		
		function updateDemand(demand){
			return DemandResource.update(demand).$promise;
		}
		
		function deleteDemand(demandID){
			return DemandResource.delete({id:demandID}).$promise;
		}
		
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('DemandsController', DemandsController);

	/** @ngInject */
	function DemandsController(DemandsService, $mdDialog, $location, $scope) {
		var vm = this;

		vm.openCreateDialog = openCreateDialog;
		vm.openViewDialog = openViewDialog;
		vm.search = {};

		function loadDemands(){
			DemandsService.getAllDemands()
				.then(function(demands){
					vm.demands = demands;
					loadFilters();
				});
		}

		function openCreateDialog($event){
			$mdDialog.show({
				controller: 'CreateDemandController',
				controllerAs: 'vm',
				templateUrl: 'app/demands/create/createDemand.html',
				targetEvent: $event,
				clickOutsideToClose:true
			})
			.finally(function() {
				loadDemands();
			});
		}
		
		function loadFilters(){
			if($location.search().search){
				vm.search.title = $location.search().search;
				$scope.accordion.toggle(0);
			}
		}
			
		function openViewDialog($event, demandID){
			$mdDialog.show({
				controller: 'ViewDemandController',
				controllerAs: 'vm',
				templateUrl: 'app/demands/view/viewDemand.html',
				targetEvent: $event,
				clickOutsideToClose:true,
				locals: {
					demandID: demandID
				}
			})
			.finally(function() {
				loadDemands();
			});
		}

		loadDemands();
	}
})();

